<template>
  <div class="page-body">
    <van-nav-bar
      fixed
      title="评价订单"
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <div class="goods-list">
      <div class="list-item" v-for="(item, index) in goodsList" :key="index">
        <!-- 商品信息 -->
        <div class="goods-info">
          <div class="goods-img">
            <img :src="item.goods_image" alt="" />
          </div>
          <div class="goods-content text-ellipsis-2">
            {{ item.goods_name }}
          </div>
          <div class="goods-trade">
            <p>¥ {{ item.total_pay_price }}</p>
            <p>x {{ item.total_num }}</p>
          </div>
        </div>

        <!-- 评分 -->
        <div class="score-row">
          <div
            class="score-item score-praise"
            :class="{ active: formData[index].score == 10 }"
            @click="setScore(index, 10)"
          >
            <div class="score">
              <span class="score-icon iconfont icon-haoping"></span>
              <span class="score-text">好评</span>
            </div>
          </div>
          <div
            class="score-item score-review"
            :class="{ active: formData[index].score == 20 }"
            @click="setScore(index, 20)"
          >
            <div class="score">
              <span class="score-icon iconfont icon-zhongping"></span>
              <span class="score-text">中评</span>
            </div>
          </div>
          <div
            class="score-item score-negative"
            :class="{ active: formData[index].score == 30 }"
            @click="setScore(index, 30)"
          >
            <div class="score">
              <span class="score-icon iconfont icon-chaping"></span>
              <span class="score-text">差评</span>
            </div>
          </div>
        </div>

        <!-- 评价内容 -->
        <div class="form-content">
          <textarea
            type="text"
            class="textarea"
            v-model="formData[index].content"
            maxlength="500"
            placeholder="请输入评价内容 (留空则不评价)"
          ></textarea>
        </div>

        <!-- 上传图片 -->
        <van-uploader
          v-model="formData[index].imageList"
          :after-read="
            (file) => {
              uploadImage(file, index);
            }
          "
          multiple
        />
      </div>
    </div>

    <!-- 底部操作按钮 -->
    <div class="footer-fixed">
      <div class="btn-wrapper">
        <div class="btn-item btn-item-main" @click="submitComment()">
          确认提交
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getGoodsListApi, uploadImageApi, submitCommentApi } from '@/api/orderList'

export default {
  name: 'AddComment',

  data () {
    return {
      orderId: this.$route.query.orderId,
      goodsList: [],

      formData: []
    }
  },

  methods: {
    // 获取订单评价商品列表
    async getGoodsList () {
      const { data: { goodsList } } = await getGoodsListApi(this.orderId)
      this.goodsList = goodsList
      this.formData = this.goodsList.map((item) => {
        return {
          goods_id: item.goods_id,
          order_goods_id: item.order_goods_id,
          score: 10,
          content: '',
          imageList: [],
          uploaded: []
        }
      })
    },

    setScore (index, score) {
      this.formData[index].score = score
    },

    async uploadImage (file, index) {
      // console.log(file, index)
      const formData = new FormData()
      formData.append('file', file.file)
      const { data: { fileInfo } } = await uploadImageApi(formData)
      // console.log(fileInfo)
      this.formData[index].uploaded.push(fileInfo.file_id)
    },

    async submitComment () {
      await submitCommentApi(this.orderId, this.formData)
      this.$router.replace(`/orderdetail?orderId=${this.orderId}`)
    }

  },

  created () {
    this.getGoodsList()
  }
}

</script>

<style lang="less" scoped>
.page-body {
  padding: 48px 0;
  background-color: #fafafa;
}
.goods-list {
  margin: 10px auto;
  width: 94%;
  color: #333;
  font-size: 13px;
  .list-item {
    padding: 15px;
    border-radius: 8px;
    background-color: #fff;
    margin-bottom: 15px;
    box-shadow: 0 0.5px 2px 0 rgba(0, 0, 0, 0.05);
  }
  .goods-info {
    display: flex;
    .goods-img {
      width: 90px;
      height: 90px;
      margin: 0px 10px 10px 0;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .goods-content {
      flex: 2;
      line-height: 18px;
      max-height: 36px;
      margin-top: 8px;
    }
    .goods-trade {
      flex: 1;
      line-height: 18px;
      text-align: right;
      color: #b39999;
      margin-top: 8px;
    }
  }

  .score-row {
    display: flex;
    justify-content: space-around;
    padding: 12px 10.2px;

    .score-item {
      display: flex;
      justify-content: center;
      align-items: center;

      &.score-praise {
        color: #ff4544;

        .score-icon {
          background: #ff4544;
        }
      }

      &.score-review {
        color: #fcb500;

        .score-icon {
          background: #fcb500;
        }
      }

      &.score-negative {
        color: #9b9b9b;

        .score-icon {
          background: #9b9b9b;
        }
      }

      .score {
        padding: 5.1px 5.1px;
        border-radius: 12px;

        .score-icon {
          margin-right: 5.1px;
          padding: 5.1px;
          border-radius: 50%;
          color: #fff;
        }
      }

      &.active {
        .score {
          color: #fff;
        }

        &.score-praise {
          .score {
            background: #ff4544;
          }
        }

        &.score-review {
          .score {
            background: #fcb500;
          }
        }

        &.score-negative {
          .score {
            background: #9b9b9b;
          }
        }
      }
    }
  }
}

// 评价内容
.form-content {
  padding: 15px 12px;

  .textarea {
    width: 100%;
    height: 96px;
    padding: 6px;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 13.2px;
  }
}

// 底部操作栏
.footer-fixed {
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 48px;
  z-index: 11;
  box-shadow: 0 -2px 20px 0 rgba(151, 151, 151, 0.24);
  background-color: #fff;
  .btn-wrapper {
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 10px;
  }

  .btn-item {
    flex: 1;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    border-radius: 25px;
  }

  .btn-item-main {
    background: linear-gradient(to right, #f9211c, #ff6335);
  }
}
</style>
